import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import Game from '../views/Game'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Main',
    component: Main
  },
  {
    path: '/Game',
    name: 'Game',
    component: Game
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router
